import React from 'react';
import { Link } from 'gatsby';
import { DAY_OF_THE_WEEK } from '@constants/time';

import {
  ActivityListTitle,
  ActivityListTitleCircle,
  ActivityListTitleContent,
  ActivityLayout,
  ActivityList,
  ActivityItem,
  ActivityItemTagItem,
  ActivityItemTagList,
  HighlightKeyword,
} from './Styles';

// 상수 파일로 분리
const BLANK = '';

interface Props {
  divideByDate: Map<number, MatchedFileType[]>;
  keyword: string;
  INCLUDE_KEYWORD: RegExp;
}

function ActivityPresenter({ divideByDate, keyword, INCLUDE_KEYWORD }: Props) {
  return (
    <ActivityLayout>
      {[...divideByDate.entries()]
        .sort((a, b) => b[0] - a[0])
        .map(([time, files]) => {
          const date = new Date(time);

          return (
            <div key={date.getTime().toString()}>
              {/** dummy post list Item */}
              <ActivityList>
                <ActivityItem>ㅤ</ActivityItem>
              </ActivityList>

              {/** post list title */}
              <ActivityListTitle>
                <ActivityListTitleCircle>
                  {DAY_OF_THE_WEEK[date.getDay()]}
                </ActivityListTitleCircle>
                <ActivityListTitleContent>
                  <span>
                    {`${date.getFullYear()}년 ${
                      date.getMonth() + 1
                    }월 ${date.getDate()}일`}
                  </span>
                </ActivityListTitleContent>
              </ActivityListTitle>

              {/** post list */}
              <ActivityList>
                {files.map((file) => {
                  const { relativeDirectory, name, ext, markdownData } = file;

                  const relativePath = relativeDirectory
                    ? `${relativeDirectory}/${name + ext}`
                    : name + ext;

                  return (
                    <ActivityItem key={relativePath}>
                      <Link to={`/post/${relativePath}`}>
                        {relativePath
                          .split(INCLUDE_KEYWORD)
                          .map((value, index) => {
                            if (
                              keyword !== BLANK &&
                              INCLUDE_KEYWORD.test(value)
                            ) {
                              // highlight 조각은 유동적으로 달라질 일이 없어 index를 키값으로 사용함.
                              return (
                                <HighlightKeyword key={index}>
                                  {value}
                                </HighlightKeyword>
                              );
                            }

                            return value;
                          })}
                      </Link>
                      {markdownData && (
                        <ActivityItemTagList>
                          {markdownData.frontmatter.tag &&
                            markdownData.frontmatter.tag.map((name, index) => (
                              // 태그는 유동적으로 달라질 일이 없어 index를 키값으로 사용함.
                              <ActivityItemTagItem key={index}>
                                {`#${name}`
                                  .split(INCLUDE_KEYWORD)
                                  .map((value, index) => {
                                    if (
                                      keyword !== BLANK &&
                                      INCLUDE_KEYWORD.test(value)
                                    ) {
                                      // highlight 조각은 유동적으로 달라질 일이 없어 index를 키값으로 사용함.
                                      return (
                                        <HighlightKeyword key={index}>
                                          {value}
                                        </HighlightKeyword>
                                      );
                                    }

                                    return value;
                                  })}
                              </ActivityItemTagItem>
                            ))}
                        </ActivityItemTagList>
                      )}
                    </ActivityItem>
                  );
                })}
              </ActivityList>
            </div>
          );
        })}
    </ActivityLayout>
  );
}

export default ActivityPresenter;
